.grid-master {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 20vh 1fr 1fr 20vh;
  grid-template-areas:
    "title title"
    "main main"
    "buttonbox buttonbox"
    "sidebar sidebar"
    "footer masthead";
  grid-gap: .3em;
  font-family: monospace;
  font-size: 14px;
}

@media screen and (min-width: 600px) {
  .grid-master {
    display: grid;
    position: fixed;
    grid-template-columns: 1fr;
    grid-template-rows: 100px 1fr;
    grid-template-areas:
      "title"
      "main";
      margin: .3em;
      width: calc(100% - .6em);
    max-height: 100vh;
    min-height: calc(100vh - .6em);
  }
}

.page-header {
  grid-area: title;
  border-radius: 10px;
  background-color: rgba(0, 50, 205. 0.3);
}

.main-content {
  grid-area: main;
  border-radius: 10px;
  background-color: rgba(250, 0, 45. 0.3);
}



