.marker-machine {
  display: grid;
  position: fixed;
  left: 20px;
  bottom: 20px;
  grid-template-columns: 400px 1fr 1fr;
  grid-template-rows: 260px 1fr 100px;
  grid-template-areas:
    "mm-player mm-sidebar mm-sidebar"
    "mm-buttonbox mm-sidebar mm-sidebar"
    "mm-footer mm-footer mm-footer";
  /* margin: .3em; */
  width: calc(100% - 40px);
  max-height: calc(100vh - (125px));;
  min-height: calc(100vh - (125px));
  margin: auto;
}

.mm-player {
  grid-area: mm-player;
  background-color: rgba(15, 10, 25, 0.2);
}

.mm-buttonbox {
  grid-area: mm-buttonbox;
  background-color: rgba(25, 0, 25, 0.2);
}

.mm-sidebar {
  grid-area: mm-sidebar;
  background-color: rgba(25, 25, 25, 0.2);
}

.mm-footer {
  grid-area: mm-footer;
  background-color: rgba(55, 55, 55, 0.2);
}

.mm-gridbox {
  border-radius: 10px;
  padding-left: 1em;
  margin: 5px;
}



