* {
  margin: 0;
}

.demo1 {
  width: 100%;
  height: 100%;
  position: fixed;
}

.demo1-ball {
  border-radius: 99px;
  background-color: white;
  width: 50px;
  height: 50px;
  border: 1px solid white;
  position: absolute;
  background-size: 50px;
}

.ball-0 {
  background-color: rgba(255,5,200,0.6);
}

.ball-1 {
  background-color: rgba(255,255,0,0.6);}

.ball-2 {
  background-color: rgba(255,0,20,0.6);}

.ball-3 {
  background-color: rgba(5,55,200,0.4);
}

.ball-4 {
  background-color: rgba(55,5,200,0.5);
}

.ball-5 {
  background-color: rgba(5,255,150,0.4);
}

.ball-6 {
  background-color: rgba(255,155,0,0.5);
}



