.header-nav {
  display: grid;
  position: relative;
  z-index: 1;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas:
    "headertitle headerlinks"
}

.header-title {
  grid-area: headertitle;
  font-size: 50px;
  padding-left: 20px;
  overflow: visible;
  position: fixed;
  top: 40px;
}

.header-links {
  display: inline;
  grid-area: headerlinks;
  color: red;
  padding-top: 60px;
  padding-right: 20px;
}



