.thegrid-master {
  display: grid;
  position: fixed;
  left: 20px;
  bottom: 20px;
  grid-template-columns: 400px 1fr 1fr;
  grid-template-rows: 260px 1fr 100px;
  grid-template-areas:
    "tg-1 tg-2 tg-2"
    "tg-3 tg-2 tg-2"
    "tg-4 tg-4 tg-4";
  width: calc(100% - 40px);
  max-height: calc(100vh - (125px));;
  min-height: calc(100vh - (125px));
  margin: auto;
}

.tg-1 {
  grid-area: tg-1;
  background-color: rgba(15, 10, 25, 0.2);
}

.tg-2 {
  grid-area: tg-2;
  background-color: rgba(25, 0, 25, 0.2);
}

.tg-3 {
  grid-area: tg-3;
  background-color: rgba(25, 25, 25, 0.2);
}

.tg-4 {
  grid-area: tg-4;
  background-color: rgba(55, 55, 55, 0.2);
}

.tg-box {
  border-radius: 10px;
  padding-left: 1em;
  margin: 5px;
}



